<template>
    <div class="search-page">
        <div class="main-container">
            <div class="search-container">
                <form @submit.prevent="searchinputValueChanged">
                    <p>{{ props.SearchFor }}</p>
                    <div class="search-bar">
                        <input type="text"
                               v-model="mainSearchKeyword"
                               :placeholder="props.SearchForLabelPlaceholder" />
                        <i class="icon-search-icon"
                           @keyup.enter.prevent="searchinputValueChanged"
                           @click.prevent="searchinputValueChanged"></i>
                    </div>
                </form>
            </div>
            <div class="keywords-container"
                 v-if="searchCategories != null && searchCategories.length > 0">
                <swiper :navigation="true"
                        @swiper="setThumbsSwiper"
                        v-bind="swiperParams"
                        :modules="modules"
                        class="mySwiper"
                        :prevent-clicks="false"
                        :prevent-clicks-propagation="false">
                    <swiper-slide @click="selectCategory(item.id)"
                                  v-for="(item, index) in searchCategories"
                                  :key="index"
                                  :id="item.id"
                                  :data-id="item.id"
                                  :data-index="index"
                                  :class="{ 'active-keyword': item.id == generatedQuery.categoryId }">
                        {{ item.name }}
                    </swiper-slide>
                </swiper>
                <div class="swiper-button-next">
                    <div class="next">
                        <div class="icon-right-custom icon-right-custom"></div>
                    </div>
                </div>
                <div class="swiper-button-prev">
                    <div class="previous">
                        <div class="icon-right-custom icon-right-custom--flip"></div>
                    </div>
                </div>
            </div>
            <div class="search-results-container"
                 v-if="detailsData.searchResults != null && isApiCalled">
                <div class="search-results">
                    <div class="search-result"
                         v-if="detailsData.searchResults?.length > 0"
                         :key="attmp">
                        <section class="search-result"
                                 v-for="(searchResult, index) in detailsData.searchResults"
                                 :key="index"
                                 :class="{
                'search-result--category-selected':
                  generatedQuery.categoryId != '',
              }">
                            <SearchCard v-if="detailsData.searchResults?.length > 0"
                                        :title="searchResult.title"
                                        :description="searchResult.description"
                                        :url="searchResult.url"
                                        :key="attmp"></SearchCard>
                        </section>
                    </div>
                </div>
            </div>
            <div class="row"
                 v-if="!isLoading && detailsData.searchResults?.length == 0">
                <div class="col-md-12 genericListing--min-height-section">
                    <div class="genericListing__empty-message generic-empty-message">
                        <img src="../../assets/gold-no-message.svg"
                             :alt="props.noResultFound" />
                        <h3>{{ props.noResultFound }}</h3>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-md-12" v-if="isLoading">
                    <div class="genericListing__generic-card-contents-loader">
                        <Loader :show="isLoading" />
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-12">
                    <div id="generic-observer"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Swiper, SwiperSlide } from "swiper/vue/swiper-vue";
    import "swiper/swiper-bundle.css";
    import { Navigation } from "swiper";
    import axios from "axios";
    import Loader from "../global/components/loader/Loader.vue";
    import SearchCard from "./SearchCard.vue";
    let getSearchParams;
    export default {
        components: {
            Swiper,
            SwiperSlide,
            Loader,
            SearchCard,
        },
        data() {
            return {
                swiperParams: {
                    loop: false,
                    slidesPerView: "auto",
                    simulateTouch: true,
                    spaceBetween: 30,
                    allowTouchMove: false,
                    Autoplay: false,
                    navigation: {
                        nextEl: ".swiper-button-next",
                        prevEl: ".swiper-button-prev",
                    },
                    breakpoints: {
                        // when window width is >= 320px
                        320: {
                            spaceBetween: 15,
                        },
                        // when window width is >= 480px
                        768: {
                            spaceBetween: 15,
                        },
                        // when window width is >= 640px
                        1024: {
                            spaceBetween: 15,
                        },
                        1025: {
                            spaceBetween: 30,
                        },
                    }, // for less than 320 default will be applied
                },
                props: {},
                thumbsSwiper: null,
                isInitedBefore: false,
                isLoading: false,
                dataFinished: false,
                fullySearchWithLoadOnScroll: false,
                searchCategories: null,
                detailsData: {},
                mainSearchKeyword: "",
                generatedQuery: {
                    pageNumber: 1,
                    Culture: "",
                    searchKeyWord: "",
                    categoryId: "",
                },
                nmberOfPages: 0,
                allResult: [],
                isApiCalled: false,
                attmp: 0,
                filteredList: [],
            };
        },
        methods: {
            setThumbsSwiper(swiper) {
                this.thumbsSwiper = swiper;
            },
            selectCategory(categoryId) {
                this.isApiCalled = true;
                this.generatedQuery.pageNumber = 1;
                this.fullySearchWithLoadOnScroll = true;
                this.dataFinished = true;
                if (categoryId == this.generatedQuery.categoryId) {
                    this.generatedQuery.categoryId = "";
                    this.filteredList = [];
                    this.nmberOfPages = Math.ceil(this.allResult.length / 8);
                    this.detailsData.searchResults = this.loadOnScroll(this.allResult);
                    this.attmp++;
                } else {
                    this.generatedQuery.categoryId = categoryId;
                    this.filteredList = this.allResult.filter((item) => {
                        return item.categoryId == this.generatedQuery.categoryId;
                    });
                    this.attmp++;
                    this.nmberOfPages = Math.ceil(this.filteredList.length / 8);
                    if (this.filteredList.length > 8) {
                        this.detailsData.searchResults = this.loadOnScroll(this.filteredList);
                    } else {
                        this.detailsData.searchResults = this.filteredList;
                    }
                }
                this.isLoading = false;
            },
            callApi() {
                this.isLoading = true;
                this.isInitedBefore = false;
                axios
                    .get(
                        `${window.location.origin}/Search/GetAllDataGeneric?pageNumber=${this.generatedQuery.pageNumber}&Culture=${this.generatedQuery.Culture}&searchKeyWord=${this.generatedQuery.searchKeyWord}&categoryId=${this.generatedQuery.categoryId}`,
                        this.generatedQuery
                    )
                    .then((res) => {
                        if (
                            res.data.searchCategories != null &&
                            res.data.searchCategories.length > 0
                        ) {
                            this.searchCategories = res.data.searchCategories;
                        } else {
                            this.searchCategories = [];
                        }
                        if (res.data.searchResults?.length == 0) {
                            this.detailsData.searchResults = [];

                            this.fullySearchWithLoadOnScroll = false;
                        } else if (res.data.searchResults?.length != 0) {
                            this.allResult = res.data.searchResults;
                            this.isApiCalled = true;
                            this.nmberOfPages = Math.ceil(res.data.searchResults.length / 8);

                            this.detailsData.searchResults = this.loadOnScroll(this.allResult);
                            this.isLoading = false;

                            if (res.data.searchResults === this.detailsData.searchResults) {
                                this.dataFinished = false;
                                this.fullySearchWithLoadOnScroll = false;
                                this.isLoading = false;
                            } else {
                                this.dataFinished = true;
                                this.fullySearchWithLoadOnScroll = true;
                                this.isLoading = true;
                            }
                        }


                        this.isLoading = false;
                        this.isInitedBefore = true;
                        this.dataFinished = true;
                    })
                    .catch(() => {
                        document.querySelector("#snackbar").innerText =
                            this.props.somethingWentWrongLabel;
                        document.querySelector("#snackbar").classList.add("show");
                        setTimeout(function () {
                            document.querySelector("#snackbar").classList.remove("show");
                        }, 3000);
                        this.isLoading = false;
                    });
            },
            checkObserverTrigger() {
                const options = {
                    threshold: 1.0,
                };
                const checker = document.querySelector("#generic-observer");
                const infiniteScroll = (entries) => {
                    entries.forEach((entry) => {
                        if (entry.isIntersecting && this.isInitedBefore) {
                            if (
                                this.detailsData != "undefined" &&
                                this.detailsData.searchResults.length != 0 &&
                                this.generatedQuery.pageNumber >= 1 &&
                                this.dataFinished &&
                                this.fullySearchWithLoadOnScroll
                            ) {
                                setTimeout(() => {
                                    if (this.nmberOfPages >= 1) {
                                        this.nmberOfPages = this.nmberOfPages - 1;
                                        this.generatedQuery.pageNumber++;
                                        if (this.filteredList?.length) {
                                            this.detailsData.searchResults.push(
                                                ...this.loadOnScroll(this.filteredList)
                                            );
                                        } else {
                                            this.detailsData.searchResults.push(
                                                ...this.loadOnScroll(this.allResult)
                                            );
                                        }

                                        this.isLoading = false;
                                    }
                                }, 500);
                            }
                        }
                    });
                };

                const observer = new IntersectionObserver(infiniteScroll, options);
                observer.observe(checker);
            },
            searchinputValueChanged() {
                if (this.mainSearchKeyword.trim().length >= 3) {
                    //Set search Params here
                    getSearchParams = new URLSearchParams(window.location.search);
                    getSearchParams.set("searchkeyword", this.mainSearchKeyword);
                    let newurl =
                        window.location.protocol +
                        "//" +
                        window.location.host +
                        window.location.pathname +
                        "?" +
                        getSearchParams.toString();
                    window.history.replaceState({ path: newurl }, "", newurl);
                    this.generatedQuery.searchKeyWord = this.mainSearchKeyword;
                    this.setHeaderSearchInputText();
                    this.dataFinished = false;
                    this.generatedQuery.pageNumber = 1;
                    this.detailsData.searchResults = [];
                    this.fullySearchWithLoadOnScroll = true;
                    this.generatedQuery.categoryId = "";

                    this.callApi();
                }
            },
            getSearchTextQueryString() {
                getSearchParams = new URLSearchParams(window.location.search);
                let searchText = getSearchParams.get("searchkeyword");
                if (
                    searchText != null &&
                    searchText != "" &&
                    searchText.trim().length >= 3
                ) {
                    this.generatedQuery.searchKeyWord = searchText;
                    this.mainSearchKeyword = searchText;
                    //Set Header search input text
                    this.setHeaderSearchInputText();
                    this.callApi();
                } else {
                    this.mainSearchKeyword = searchText;
                    if (!this.isInitedBefore) {
                        document.querySelector(".navbar-form__group--desktop input").value =
                            this.mainSearchKeyword;
                        document.querySelector(".navbar-form--sm input").value =
                            this.mainSearchKeyword;
                        this.searchCategories = [];
                        this.detailsData.searchResults = [];
                    }
                }
            },
            setHeaderSearchInputText() {
                document.querySelector(".navbar-form__group--desktop input").value =
                    this.generatedQuery.searchKeyWord;
                document.querySelector(".navbar-form--sm input").value =
                    this.generatedQuery.searchKeyWord;
            },
            loadOnScroll(list) {
                let startIndex = (this.generatedQuery.pageNumber - 1) * 8;
                let endIndex = startIndex + 8 - 1;
                const SlicedEightItems = list.slice(startIndex, endIndex + 1);
                return SlicedEightItems;
            },
        },
        mounted() {
            this.props = this.$attrs.props;
            this.generatedQuery.Culture = this.props.lang;
            this.checkObserverTrigger();
            this.getSearchTextQueryString();
        },
        setup() {
            return {
                modules: [Navigation],
            };
        },
        watch: {},
    };
</script>


